import React from 'react';
import OptionCard from '../../Common/OptionCard/OptionCard';
import { getStorage, setStorage } from '../../../Utility/helpers';

import './UserLevel.css';
const UserLevel = ({ handleOnboarding, setStep }) => {
  const handleClick = async (val) => {
    const networkType = await getStorage('networkProfile');
    setStorage('userMode', val);
    setStorage('onboarded', true);
    if (networkType === 'wifi' || networkType === 'ethernet') {
      //check for updates
      setStep(4);
    } else {
      handleOnboarding(val);
    }
  };
  return (
    <div className="menu-container">
      <h3>Please choose a mode</h3>
      <div className="menu-options-container">
        <OptionCard
          title="Beginner"
          description="For beginners. We will walk you through how to operate your 3D
                printer, step by step."
          onClick={() => handleClick('beginner')}
        />
        <OptionCard
          title="Standard"
          description="For users familiar with 3D printing."
          onClick={() => handleClick('standard')}
        />
      </div>
    </div>
  );
};

export default UserLevel;
