import React, { useState } from "react";
import {
  Button,
  Grid,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  ListItemSecondaryAction,
  ListSubheader,
  Select,
  Snackbar,
  Switch,
  Typography
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import {
  Class,
  LocalLibrary,
  Wifi,
  WifiTethering,
  SettingsEthernet,
  SignalCellularNull
} from "@material-ui/icons/";

import { useMountEffect } from "../../../Utility/useMountEffect";
import { getStorage, setStorage } from "../../../Utility/helpers";
import NetworkSettings from "../../Setup/NetworkType/NetworkSettings";
import NetworkType from "../../Setup/NetworkType/NetworkType";

import WifiConnect from "../../Setup/NetworkType/WifiConnect";

const networkOptions = {
  wifi: {
    name: "Wi-Fi",
    icon: <Wifi />
  },
  hotspot: {
    name: "Hotspot",
    icon: <WifiTethering />
  },
  ethernet: {
    name: "Ethernet",
    icon: <SettingsEthernet />
  },
  none: {
    name: "None",
    icon: <SignalCellularNull />
  }
};

const useStyles = makeStyles({});
const Settings = () => {
  const [networkExpanded, setNetworkExpanded] = useState(false);
  const [networkType, setNetworkType] = useState("none");
  const [printerMode, setPrinterMode] = useState("");
  const [userMode, setUserMode] = useState("");
  const [displayWifi, setDisplayWifi] = useState(false);
  const [displaySSID, setDisplaySSID] = useState(false);
  const [step, setStep] = useState(0);

  const [showSnack, setShowSnack] = useState(false);
  const [snackMessage, setSnackMessage] = useState("");
  const handleSnackClose = () => {
    setShowSnack(() => false);
    setSnackMessage("");
  };

  const classes = useStyles();

  useMountEffect(() => {
    setNetworkType(() => {
      const networkProfile = getStorage("networkProfile");
      return networkProfile !== null ? networkProfile : "none";
    });
    setPrinterMode(() => getStorage("printerProfile"));
    setUserMode(() => getStorage("userMode"));
  });

  const handleNetworkToggle = (value) => {
    const network = value.toLowerCase();
    setNetworkExpanded(false);
    setNetworkType(network);
    setStorage("networkProfile", network);
    setStep(0);
  };

  const setNewType = (value) => {
    const network = value.toLowerCase();
    setNetworkType(network);
  };
  return (
    <>
      <Grid container direction="column" className={classes.container}>
        <Grid>
          <List
            subheader={<ListSubheader>Network</ListSubheader>}
            className={classes.root}
          >
            <ListItem
              button
              onClick={() => {
                if (networkType === "wi-fi") {
                  setDisplayWifi((prev) => !prev);
                } else if (
                  networkType === "hotspot" ||
                  networkType === "ethernet"
                ) {
                  setDisplaySSID((prev) => !prev);
                } else {
                  return;
                }
              }}
            >
              <ListItemIcon>
                {networkOptions[networkType].icon || <Wifi />}
              </ListItemIcon>
              <ListItemText primary={networkOptions[networkType].name} />
              <ListItemSecondaryAction>
                <Button
                  variant="contained"
                  color={networkExpanded ? "secondary" : "primary"}
                  onClick={() => {
                    setStep(2);
                  }}
                  size={networkExpanded ? "small" : "medium"}
                >
                  {networkExpanded ? "Select" : "Change"}
                </Button>
              </ListItemSecondaryAction>
            </ListItem>
            {networkExpanded && (
              <>
                {Object.entries(networkOptions).map((option) => {
                  const obj = option[1];
                  if (networkType !== option[0]) {
                    return (
                      <ListItem key={`${option[0]}-exanded-network`}>
                        <ListItemIcon>{obj.icon}</ListItemIcon>
                        <ListItemText primary={obj.name} />
                        <ListItemSecondaryAction>
                          <Button
                            variant="contained"
                            color="secondary"
                            onClick={() => handleNetworkToggle(obj.name)}
                            size="small"
                          >
                            Select
                          </Button>
                        </ListItemSecondaryAction>
                      </ListItem>
                    );
                  }
                })}
              </>
            )}
          </List>
        </Grid>
        {displaySSID && (
          <Grid>
            Your SSID is
            <Typography color="secondary" variant="body1">
              Maker300X
            </Typography>
            Your password is
            <Typography color="secondary" variant="body1">
              makermade
            </Typography>
            {networkType === "ethernet" && (
              <Typography variant="body2">
                Don't forget to connect your cable!
              </Typography>
            )}
          </Grid>
        )}
        {displayWifi && (
          <Grid item xs>
            <WifiConnect
              setShowSnack={setShowSnack}
              setSnackMessage={setSnackMessage}
              handleSnackClose={handleSnackClose}
              settingsScreen={true}
            />
          </Grid>
        )}

        <Grid>
          <List
            subheader={<ListSubheader>User Type</ListSubheader>}
            className={classes.root}
          >
            <ListItem>
              <ListItemIcon>
                <LocalLibrary />
              </ListItemIcon>
              <ListItemText primary="Experience Level" />
              <Select
                native
                value={userMode}
                onChange={(e) => {
                  if (e.target && e.target.value) {
                    setUserMode(e.target.value);
                    setStorage("userMode", e.target.value);
                  }
                }}
                MenuProps={{
                  anchorOrigin: {
                    vertical: "bottom",
                    horizontal: "left"
                  },
                  getContentAnchorEl: null
                }}
              >
                <option value="beginner">Beginner</option>
                <option value="standard">Standard</option>
              </Select>
            </ListItem>
          </List>
        </Grid>
        <Grid>
          <List
            subheader={<ListSubheader>Printer</ListSubheader>}
            className={classes.root}
          >
            <ListItem>
              <ListItemIcon>
                <Class />
              </ListItemIcon>
              <ListItemText primary="Classroom Mode" />
              <ListItemSecondaryAction>
                <Switch
                  edge="end"
                  onChange={(e) => {
                    const checkedValue = e.target.checked
                      ? "classroom"
                      : "home";
                    setPrinterMode(() => checkedValue);
                    setStorage("printerProfile", checkedValue);
                  }}
                  checked={printerMode !== "home"}
                />
              </ListItemSecondaryAction>
            </ListItem>
            <ListItem>
              <ListItemIcon>
                <Class />
              </ListItemIcon>
              <ListItemText primary={`Printer Name: ${getStorage("hostname")}`} />
              <ListItemSecondaryAction>
                <Button
                  variant="contained"
                  color={networkExpanded ? "secondary" : "primary"}
                  onClick={() => {
                    setStep(1);
                  }}
                  size={networkExpanded ? "small" : "medium"}
                >
                  {networkExpanded ? "Select" : "Change"}
                </Button>
              </ListItemSecondaryAction>
            </ListItem>
          </List>
        </Grid>
        <Snackbar
          open={showSnack}
          autoHideDuration={6000}
          message={snackMessage}
          onClose={handleSnackClose}
        ></Snackbar>
      </Grid>
      {step === 2 && (
        <div
          style={{
            position: "fixed",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: "rgb(66,66,66)",
            zIndex: 8000,
            paddingLeft: 24,
            paddingRight: 24
          }}
        >
          <NetworkType
            setStep={setStep}
            setShowSnack={setShowSnack}
            setSnackMessage={setSnackMessage}
            handleSnackClose={handleSnackClose}
            setNewType={(type) => {
              setNewType(type);
            }}
          />
        </div>
      )}
      {step === 1 && (
        <div
          style={{
            position: "fixed",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: "rgb(66,66,66)",
            zIndex: 8000,
            paddingLeft: 24,
            paddingRight: 24
          }}
        >
          <NetworkSettings
            setStep={setStep}
            setShowSnack={setShowSnack}
            setSnackMessage={setSnackMessage}
            handleSnackClose={handleSnackClose}
            setNewType={(type) => {
              setNewType(type);
            }}
          />
        </div>
      )}
    </>
  );
};

export default Settings;
